import Link from "next/link";
import { formatarData } from "../app/lib/eventos";
import { formatarPreco } from "../app/lib/eventos";
import { Evento } from "../app/lib/eventos";

export default function EventCard({ evento }: { evento: Evento }) {
  return (
    <Link href={`/eventos/${evento.id}`} className="cardlink">
      <div className="eventcard">
        <div
          className="thumb"
          style={{ backgroundImage: `url(${evento.imagem})` }}
          aria-label={`Imagem do evento ${evento.titulo}`}
        />
        <div className="info">
          <div className="title">{evento.titulo}</div>
          <div className="mutedline">{evento.artista}</div>

          <div className="meta">
            <span className="badge">{formatarData(evento.data)}</span>
            <span className="badge">{evento.horario}</span>
            <span className="badge">{formatarPreco(evento.precoInicial)}</span>
          </div>
        </div>
      </div>
    </Link>
  );
}
